.music_list .delete {
    cursor: pointer;
    height: 30px;
    width: 30px;
    top: 15px;
    position: absolute;
    right: 0;
    margin-right: 15px;
}

.music_list .rank {
    display: inline-block;
    width: 5%;
    font-weight: 400;
}

.music_list .title {
    display: inline-block;
    width:30%;
}

.music_list .author {
    display: inline-block;
    width:30%;
    font-size: 14px;
    color: #4e555b;
}






